.calendar-event,
.news-event {
    display: flex;
    flex-direction: column;
    padding-bottom: 30px;
    margin-bottom: 30px;
    border-bottom: 1px solid #666;
    
    @include min-screen($breakpoint-small) {
        padding-bottom: 60px;
        margin-bottom: 60px;
        flex-direction: row;
    }
}

.calendar-event__image,
.news-event__image {
    padding-right: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    
    img {
        width: 100px;
        margin-bottom: 30px;
    }
    
    @include min-screen($breakpoint-small) {
        padding-right: 110px;

        img {
            width: 230px;
        }
    }
}

.calendar-event__social {
    display: flex;
    align-items: center;
    justify-content: center;
    
    a {
        display: block;
        width: 46px;
        margin: 0 10px;
        
        img {
            width: 100%;
        }
        
        @include min-screen($breakpoint-small) {
            margin: 0 20px;
        }
    }
}

.calendar-event__content,
.news-event__content {
    flex: 1;

    h2, h3 {
        margin-bottom: 0;
        line-height: 1.3;
    }

    time {
        color: $color-lightgray;
    }

    h4 {
        margin: 30px 0;
        
        @include min-screen($breakpoint-small) {
            margin: 70px 0;
        }
    }

}

.news-event__content {
    h2 {
        margin-bottom: 20px;
    }

    time {
        margin-bottom: 20px;
        display: block;
    }
}